body {
    margin: 0;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

select {
    display: block;
    font-size: 16px;
    font-family: sans-serif;
    font-weight: 700;
    color: #444;
    line-height: 1.3;
    padding: 5px 15px;
    max-width: 100%;
    box-sizing: border-box;
    margin: 0;
    border: 1px solid #aaa;
    box-shadow: 0 1px 0 1px rgba(0, 0, 0, .04);
    border-radius: .5em;
    -moz-appearance: none;
    -webkit-appearance: none;
    appearance: none;
    background-image: url('data:image/svg+xml;charset=US-ASCII,%3Csvg%20xmlns%3D%22http%3A%2F%2Fwww.w3.org%2F2000%2Fsvg%22%20width%3D%22292.4%22%20height%3D%22292.4%22%3E%3Cpath%20fill%3D%22%23007CB2%22%20d%3D%22M287%2069.4a17.6%2017.6%200%200%200-13-5.4H18.4c-5%200-9.3%201.8-12.9%205.4A17.6%2017.6%200%200%200%200%2082.2c0%205%201.8%209.3%205.4%2012.9l128%20127.9c3.6%203.6%207.8%205.4%2012.8%205.4s9.2-1.8%2012.8-5.4L287%2095c3.5-3.5%205.4-7.8%205.4-12.8%200-5-1.9-9.2-5.5-12.8z%22%2F%3E%3C%2Fsvg%3E'), linear-gradient(to bottom, #cdb467 0%, #e0dcdc 100%);
    background-repeat: no-repeat, repeat;
    background-position: right .7em top 50%, 0 0;
    background-size: .65em auto, 100%;
}

select::-ms-expand {
    display: none;
}

select:focus,
select:hover {
    border-color: #aaa;
    box-shadow: 0 0 1px 3px rgb(255, 194, 0);
    box-shadow: 0 0 0 3px -moz-mac-focusring;
    color: #222;
    outline: none;
}

select-css option {
    font-weight: normal;
}

*[dir="rtl"] select, :root:lang(ar) select, :root:lang(iw) select {
    background-position: left .7em top 50%, 0 0;
    padding: .6em .8em .5em 1.4em;
}

.App {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 5px 0 15px 0;
    flex-grow: 1;
    min-height: 100vh;
    width: 100%;
    background: linear-gradient(90deg, #bb7387 0%, #72717f 100%);
}

.Header {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
    max-width: 700px;
}

.HeaderTitle {
    width: 100%;
    display: flex;
    justify-content: center;
    position: relative;
    z-index: 10;
}

.HeaderTitle:after {
    box-sizing: border-box;
    position: absolute;
    top: 50%;
    left: 0;
    width: 100%;
    border: 1px solid #ffbd04;
    content: '';
    z-index: -1
}

.HeaderTitle h3 {
    text-align: center;
    background: #7e96cb;
    border: 1px solid #b4acac;
    border-radius: 10px;
    width: 300px;
    font-size: 20px;
}

.HeaderMenu {
    display: flex;
    margin-bottom: 10px;
}

.HeaderMenuItem {
    box-sizing: border-box;
    padding: 0 5px;
    margin: 0 15px;
    cursor: pointer;
    border: 2px solid #bebebe;
    border-radius: 5px;
    background: #656361;
    box-shadow: 2px 2px 2px rgba(0, 0, 0, 0.5);
    transition: background 0.5s ease-in-out;
}

.HeaderMenuItem:hover {
    background: #ffbd04;
}

.HeaderMenuItem.active {
    background: #15ff04;
}

.HeaderDescription {
    cursor: pointer;
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 0 5px 5px;
    max-width: 300px;
    border: 1px solid #0e0606;
    box-shadow: 2px 2px 2px rgba(0, 0, 0, 0.5);
}

.HeaderDescription a {
    text-decoration: none;
    padding-bottom: 5px;
    max-width: 300px;
}

.HeaderDescription a:hover {
    background: #e77373;
}

.HeaderDescription span {
    color: #263226;
}

.HeaderDescription img {
    width: 100%;
    opacity: 0.8;
    max-width: 300px; /*ie 11*/
}

.HeaderDescription h3 {
    margin: 0;
    padding: 5px;
    text-align: center;
    font-family: Bradley Hand, cursive;
    font-size: 16px;
    color: #ccc9e0;
    max-width: 300px; /*ie 11*/
}

.HeaderDescription div {
    width: 100%;
    display: flex;
    justify-content: space-around;
    text-decoration: none;
    max-width: 300px; /*ie 11*/
}

.Spacer {
    margin-top: 10px;
    max-width: 700px;
    width: 100%;
    height: 2px;
    border-top: 1px solid #bebebe;
    border-bottom: 1px solid #bebebe;
}

.content {
    padding: 5px;
    max-width: 700px;
}

.content-select {
    border: 1px solid #f1f1f5;
    border-radius: 10px;
    padding: 5px;
    font-size: 16px;
    color: #ffc200;
    background: #453e3e;
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    box-shadow: 2px 2px 2px rgba(0, 0, 0, 0.5);
}

.content-select div,
.content-select select {
    margin-bottom: 10px;
    max-width: 100%; /*ie 11*/
}

.content-select span {
    color: red;
}